import React from 'react';
import { NavLink } from 'react-router-dom';

export default function ClothingList() {
    return (
        <div className='blockClothingList'>
            <ul>
                <li><NavLink to="/category/jeans"></NavLink></li>
                <li><NavLink to="/category/tracksuits"> </NavLink></li>
                <li><NavLink to="/category/jacket"> </NavLink></li>
                <li><NavLink to="/category/tShirts">  </NavLink></li>
                <li><NavLink to="/category/sweatshirts"></NavLink></li>
            </ul>
        </div>
    )
}
